div .randObjImg {
    width: 100%;
    max-width: 200px;
    flex-direction: row;
}

div .flex-container {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 5%;
    justify-content: space-around;
}

div .flavor-text {
    margin: 15px;
}

button {
    padding: 15px 32px;
    display: inline-block;
    font-size: 16px;
    border: none;
    border-radius: 4px;
}

img {
    border-radius: 20px;
}